@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;1,500&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Raleway', sans-serif;
    font-style: normal;
    font-weight: 400;
}

.header {
    width: 100%;
    height: 7.5rem;
    display: flex;
    align-items: center;
    padding: 0 calc(150/1440*100vw);
    background: linear-gradient(to right, #FFFFFF 50%, #F1EADC 50%);
}

.header__name{
    flex-basis: 33%;
    display: flex;
    justify-content: left;
}

.header__name h1{
    font-weight: 500;
    font-size: calc(24/1440*100vw);
    line-height: 125%;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    margin-left: 3%;
}

.header__navbar{
    display: flex;
    justify-content: space-between;
    flex-basis: 30%;
}

.header__navbar p{
    font-size: calc(18/1440*100vw);
    line-height: 140%;
    letter-spacing: 0.02em;
    cursor: pointer;
}

.header__tel{
    flex-basis: 28%;
    display: flex;
    justify-content: right;
}

.header__tel p{
    font-size: calc(18/1440*100vw);
    line-height: 140%;
    letter-spacing: 0.02em;
    margin-left: 0.7em;
}

.header__navbar p:hover{
    font-weight: bold;
}

.header__basket{
    flex-basis: 9%;
    display: flex;
    justify-content: right;
    padding-right: 0.2em;
    cursor: pointer;
}

.header__name img, .header__basket img{
    width: 1.8rem;
    height: 1.8rem;
}

.header__tel img{
    width: 1.4rem;
    height: 1.4rem;
}


